body { 
    background: white;
}
.shake {
    animation: shake .5s 0.0125s;
  }
  
  @keyframes shake {
    from,
    to {
      transform: translate3d(0, 0, 0);
    }
  
    12.5%,
    37.5%,
    62.5%,
    87.5% {
      transform: translate(-10px);
    }
  
    25%,
    50%,
    75% {
      transform: translate(10px);
    }
  }